* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  background-color: #defcf9;
}
.App {
  font-family: sans-serif;
  text-align: center;
}

.navbar {
  width: 100%;
  background-color: #00adb5;
  padding: 3%;
  text-align: center;
  font-family: "Amatic SC", cursive;
  color: #eeeeee;
}

.searchbar {
  display: flex;
  justify-content: center;
  margin: 3%;
  padding: 3%;
}
.searchbar input {
  font-size: 1.3rem;
  padding: 2%;
  box-shadow: 2px 2px 6px grey;
  border-radius: 10px;
}
.searchbar input:hover {
  background-color: #a99ef3;
  color: #19191a;
}

.image {
  display: flex;
  justify-content: center;
  margin-top: 5%;
}

.image img {
  border-radius: 10px;
  box-shadow: 3px 3px 10px black;
}
.btn {
  display: flex;
  justify-content: center;
  margin: 5% 0;
}
.btn button {
  font-size: 1.2rem;
  padding: 1% 3%;
  cursor: pointer;
  border-radius: 4px;
  background-color: #b8b5ff;
  color: white;
  font-family: "Balsamiq Sans", cursive;
  box-shadow: 2px 2px 5px black;
}
.btn button:hover {
  background-color: #7868e6;
  color: white;
  box-shadow: 2px 2px 5px #b8b5ff;
}

.intro {
  height: 300px;
  width: 380px;
  background-color: rgb(255, 255, 255);
  color: #00adb5;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  box-shadow: 2px 2px 10px rgb(36, 34, 34);
  font-family: "PT Serif", serif;
  text-shadow: 1px 1px 1px rgb(15, 15, 15);
  border-radius: 10px;
}
